@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100&display=swap);
body {
    font-family: 'Lato', sans-serif;
    background-image: url(/React-Timer-App-using-TypeScript-/static/media/wal.c83c57ad.jpg);
    /* The image used */
    background-color: #cccccc;
    /* Used if the image is unavailable */
    background-position: center;
    /* Center the image */
    background-repeat: no-repeat;
    /* Do not repeat the image */
    background-size: 100% 100%;
    /* Resize the background image to cover the entire container */
    height: 100vh;
    /* border: 5px solid yellow; */
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    font-family: lato
}

.main-div {
    position: relative;
    margin-top: 80px;
    /* background: black; */
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: flex;
}

.mobile-phone {
    background-image: linear-gradient(#101010, #121212);
    width: 260px;
    height: 500px;
    margin-left: 100px;
    border-radius: 20%;

}

.mobile-phone img {
    width: 100%;
    height: 100%;
}

.control-buttons {
    position: absolute;
    top: 85%;
    left: 14.5%;
}

.control-buttons img {
    width: 40px;
    height: 40px;
    margin: 0 8px;
    border-radius: 50%;
    box-shadow: 1px 2px 4px blue;
    border: 1px solid blue;
}

.control-buttons img:hover{
    opacity: .9;
    /* box-shadow: 10px 20px 30px white; */
    cursor: pointer;
}

.starter,
.date-time {
    width: 180px;
    height: 180px;
    /* border: 2px solid wheat; */
    margin: 30px 40px;
    position: relative;
}

.starter {
    overflow: hidden;
    border-radius: 50%;
    margin-top: 60px;
    margin-left: 100px;
}

.starter img,
.date-time img {
    width: 100%;
    height: 100%;
}

.starter span {
    /* text-align: center; */
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    color: wheat;
    font-size: 52px;
    font-weight: bold;
    background: black;
    width: 100px;
    height: 100px;
    position: absolute;
    top: 24%;
    left: 23%;
}

.date-time {
    width: 220px;
    height: 220px;
    border-radius: 50%;
}

.date-time span {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    color: wheat;
    font-size: 19.5px;
    font-weight: bold;
    /* background: skyblue; */
    width: 120px;
    height: 100px;
    position: absolute;
    top: 27%;
    left: 24.5%;
}

@media screen and (max-width: 800px) {
body{
    background-attachment: none;
    display: block;
    /* justify-content: left; */
}

.smart-phone{
    display: none;
}
.mobile-phone {
    /* border: 2px solid yellow; */
    background-image: linear-gradient(#101010, #121212);
    width: 100%;
    height: 100vh;
    margin:0px;
    border-radius: 0%;

}
.main-div {
    /* border: 2px solid yellow; */
    width: 100%;
    height: 100vh;
    margin: 0px;
    display: block;
}
}

.recent-div{
    /* border: .5px solid wheat; */
    margin: auto 20px;
    /* display: flex; */
    position: relative;
    top: -46%;
    height: 137px;
    overflow: auto;
    padding: 0px 2px;
}
.recent{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 100%;
    font-size: 18px;
    color: wheat;
    height: 40px;
    display: block;    
    margin: 4px 0;
}
.recent span{
    /* border: .5px solid wheat; */
    display: inline-block;
    width: 50%; 
    margin: 0;
    padding: 0;
    }
        /* Timing */
    .recent span:first-child{
        font-size: 22px;
        font-weight: bold;
        border-spacing: 0px;
    }
    .recent span:nth-child(2){
    text-align: right;
    }
    .recent span:nth-child(3){
    /* border: .5px solid wheat; */
    font-size: 11px;
    width: 35%;
    /* text-align: center; */
    border-spacing: 0px;
    position: relative;
    top: -9px;
    }

